* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
}

:root {
  --main-color: #330031;
  --second-main-color: #cccccc;
  --first-color: #635D63;
  --first-linear-color: #ca26ff;
  --second-linear-color: #fff3b0;
  --thierd-linear-color: #f4cac0;
  --white: #ffffff;
  --black: #111111;
}

/* HEADER STYLE STARTS */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 4rem;
  color: var(--black);
  gap: 2rem;
  text-transform: capitalize;
}

header h1 {
  font-size: 1.8rem;
}

header ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  gap: 4rem;
  flex-shrink: 0;
}

header ul li:hover {
  transform: scale(1.1);
  color: var(--first-linear-color);
  cursor: pointer;
}

header .search {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--first-color);
  overflow: hidden;
  border-radius: 30px;
  position: relative;
  box-shadow: 0 2px 5px 0 #eee;
}

header .search input {
  width: 100%;
  height: 100%;
  background-color: inherit;
  padding: 0.6rem 1rem;
  outline: 0;
  border: 0;
  color: var(--white);
}

header .search i {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

header .search input::placeholder {
  color: var(--white);
}

header .btns {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  flex-shrink: 0;
}

header .btns button {
  padding: 0.5rem 1rem;
  background-color: inherit;
  border: 0;
  outline: 1px solid var(--black);
  border-radius: 1rem;
  font-size: 0.9rem;
  text-transform: capitalize;
  cursor: pointer;
}

header .btns button:hover {
  background-color: var(--first-color);
  outline: 0;
}

header .btns button#login {
  outline: 0;
}

header .btns button#login:hover {
  outline: 1px solid var(--black);
  background-color: inherit;
}

/* HEADER STYLE ENDS */


.landing {
  background: linear-gradient(
    135deg,
    #ca26ff 7.17%,
    #f4cac0 85.48%,
    #fff3b0 100%
  );
  display: grid;
  grid-template-columns: 1fr auto;
  border-radius: 20px 20px 300px 20px;
  height: 100%;
}

.info {
  padding: 6rem 3rem;
  font-family: "Segoe UI";
  font-style: normal;
  font-weight: 700;
}

.info h1 {
  font-size: 3.8rem;
  letter-spacing: 0.125em;
  color: #330031;
  margin-bottom: 1.5rem;
}

.info h2 {
  font-size: 2.2rem;
  line-height: 0%;
  letter-spacing: 0.395em;
  color: #ffffff;
  margin-bottom: 5rem;
}

.info p {
  width: 600px;
  height: 200px;
  margin-left: 3rem;
  font-size: 1.2rem;
  line-height: 149.5%;
  letter-spacing: 0.015em;
  color: #ffffff;
}

/* End of landing page */

#cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}

#cards .card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: calc(95% / 4);
  height: 425px;
  margin-bottom: 5rem;
}

#cards .card img {
  width: 240px;
  height: 355.66px;
  border-radius: 35px;
}

#cards .card:hover img {
  cursor: pointer;
  transform: scale(0.95);
  opacity: 0.9;
}

#cards .card button {
  font-family: "Segoe UI";
  background: #330031;
  color: #ffffff;
  font-style: normal;
  width: 240px;
  padding: 0.5rem 1rem;
  font-weight: 700;
  font-size: 1.1rem;
  letter-spacing: 0.125em;
  border-radius: 35px;
  cursor: pointer;
}

#cards .card button:hover {
  background-color: inherit;
  color: #330031;
}

.div {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7rem;
  padding: 2rem 4rem;
}

.animeSectionTitle {
  margin-bottom: 5rem;
  font-size: 2rem;
  line-height: 1.6;
  border-bottom: 3px solid #330031;
}

#landing {
  padding: 2rem 5rem;
  height: 90vh;
  display: flex;
  justify-content: center;
}


#landing .details {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 90%;
  position: relative;
  height: 100%;
  border-radius: 30px;
  overflow: hidden;
}

#landing .details .image {
  background-color: rgb(8, 6, 6);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  padding: 0;
}

#landing .details .image img {
  width: 100%;
  height: 100%;
}

#landing .details .image span {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #11111150;
}

#landing .details .information {
  width: 55%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--main-color);
  position: absolute;
  right: 0;
  top: 0;
  padding: 2rem;
  overflow: auto;
  gap: 1rem;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  box-shadow: -4px 0 10px 0 var(--black);
}

#landing .details .information::-webkit-scrollbar {
  width: 10px;

}

/* Track */
#landing .details .information::-webkit-scrollbar-track {
  background: inherit;
}

/* Handle */
#landing .details .information::-webkit-scrollbar-thumb {
  background: var(--first-color);
  border-radius: 5px;
}

/* Handle on hover */
#landing .details .information::-webkit-scrollbar-thumb:hover {
  background: #555;
}

#landing .details .information h1 {
  font-size: 2rem;
  border-bottom: 1px solid var(--white);
  color: var(--white);
  text-align: center;
  padding-bottom: 1rem;
}

#landing .details .information p {
  display: flex;
  gap: 1rem;
  width: 100%;
  color: var(--white);
  align-items: center;
}

#landing .details .information p i {
  color: #dd9b00;
}

#landing .details .information p span {
  font-size: 1.2rem;
  font-weight: 700;
}

#landing .details .information .story {
  flex-direction: column;
  gap: 0.8rem;
}

#landing .details .information .story span {
  width: 100%;
}

#landing .details .information .btns {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

#landing .details .information .btns a{
  text-decoration: none;
  background-color: var(--white);
  color: var(--main-color);
  padding: 0.5rem 2rem;
  border-radius: 30px;
  text-transform: uppercase;
  font-weight: 600;
}

#landing .details .information .btns a:nth-child(1) {
  outline: 1px solid var(--white);
  color: var(--white);
  background-color: inherit;
}

#landing .details .information .btns a:nth-child(1):hover {
  background-color: var(--white);
  color: var(--main-color);
}

#landing .details .information .btns a:nth-child(2):hover {
  outline: 1px solid var(--white);
  color: var(--white);
  background-color: inherit;
}